import { defineCustomElement } from '../esm/es5/corporate-ui.core';
import * as CUI from '../esm/es5/corporate-ui.components';
export { defineCustomElements };
var CUI_COMPONENTS = CUI.COMPONENTS;

var collections = require('./collection-manifest.json');

var subComponents = {};
var collection = collections.components;
collection.forEach(function (obj) {
  if (obj.dependencies.length > 0) {
    subComponents[obj.tag] = obj.dependencies;
  }
});

function defineCustomElements(components) {
  if (components === 'all') {
    defineCustomElement(window, CUI_COMPONENTS);
  } else {
    components.forEach(function (project_comp) {
      defineCustomElement(window, [findComponent(project_comp)]);

      for (var tag in subComponents) {
        if (project_comp === tag) {
          subComponents[tag].forEach(function (tagDep) {
            defineCustomElement(window, [findComponent(tagDep)]);
          });
        }
      }
    });
  }
}

function findComponent(name) {
  var val;
  CUI_COMPONENTS.forEach(function (comp) {
    if (comp[0] === name) val = comp;
  });
  return val;
}